import initialState from '../initialState'
import * as types from '../constants/actionTypes'

const { wallet: defaultState } = initialState

module.exports = (state = defaultState, { type, payload }) => {
  switch (type) {
    case types.GET_ETHER_BALANCE_SUCCESS:
      return { ...state, etherBalance: payload }
    case types.GET_TOKEN_BALANCE_SUCCESS:
      return { ...state, tokenBalance: payload }
    case types.IGNORE_EMPTY_WALLET:
      return { ...state, ignoreEmpty: true }
    default:
      return state
  }
}



