import initialState from '../initialState'
import * as types from '../constants/actionTypes'

const { networkId: defaultState } = initialState

module.exports = (state = defaultState, { type, payload }) => {
  switch (type) {
    case types.GET_NETWORK_ID_SUCCESS:
      return payload
    default:
      return state
  }
}



